import { Component, h, Prop, State } from '@stencil/core';
import { favourites, FavouritesData } from "../../../global/data/favourites";
import { removeAllFavourites } from "../../../global/functions/favourites";
import priceFormat from '../../../global/functions/price';

declare global { interface Window { dataLayer: any[] }}

@Component({
    tag: 'ks-page-favourites',
    styleUrl: 'favourites.css'
})
export class PageRecipe{
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() favouritesData: string;
    @State() removeAllLoading: boolean = false;

    componentWillLoad() {
        const favouritesDataElement = document.getElementById(this.favouritesData);
		const favouritesData = JSON.parse(favouritesDataElement.innerHTML) as FavouritesData;

        Object.keys(favouritesData).map(key => {
			favourites.set(key as keyof FavouritesData, favouritesData[key]);
		});
    }

    removeAll() {
        if(this.removeAllLoading) return;
        this.removeAllLoading = true;
    
        removeAllFavourites().then(() => {
            location.reload();
        });
    }

	render() {
        const products = favourites.get('products');
        const descriptions = favourites.get('descriptions');

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <ks-container>
                {products?.length > 0 ?
                    <div class="favourites-container">
                        <div class="favourites-header">
                            <span class="valueString">{favourites.get('valueString')}</span>
                            <span class="value">{priceFormat(favourites.get('value'))}</span>
                            <div class="removeAll">
                                <button class="fav" onClick={()=>this.removeAll()}>
                                    { this.removeAllLoading ? <ks-loader /> : favourites.get('removeAllString') }
                                </button>
                            </div>
                        </div>

                        <hr/>

                        <div class="product-container">
                            {products?.map(card =>
                                <ks-product-fav-card summary={descriptions[card.id]} product={card} />
                            )}
                        </div>
                    </div>
                :
                    <div class="empty">
                        <h1>{favourites.get('emptyHeading')}</h1>
                        <ks-button round border name={favourites.get('emptyButton')} link="/" />
                    </div>
                }
            </ks-container>
        </ks-page-base>;
	}
}
