ks-page-favourites {
    display: block;
}

ks-page-favourites .favourites-container {
    background: #fff;
    color: #373737;
    box-shadow: 0 2px 8px rgb(0 0 0 / 15%);
    padding: 20px 30px;
}

ks-page-favourites .favourites-header {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}

ks-page-favourites .favourites-header .removeAll {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

ks-page-favourites .favourites-header .removeAll button {
    position: relative;
    max-width: 250px;
    width: 100%;

    background-color: var(--color-secondary);
    color: white;
    border-style: none;
    height: 50px;
    border-radius: 50px;

    font-family: var(--font-emphasis);
    font-size: 16px;
    line-height: 16px;

    transition: background-color 0.3s ease;
}

ks-page-favourites .favourites-header .removeAll button:hover {
    background-color: var(--color-secondary-hover);
}

ks-page-favourites .favourites-header .removeAll button:active {
    background-color: var(--color-secondary-active);
}

ks-page-favourites .favourites-header > span {
    font-size: 26px;
    line-height: 26px;
    font-weight: 700;
    font-family: var(--font-emphasis);
    white-space: nowrap;
}

ks-page-favourites .favourites-header .valueString {
    margin-right: 10px;
}

ks-page-favourites .favourites-header .value {
    color: var(--color-secondary);
}

ks-page-favourites .empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1 0 auto;
    min-height: 320px;
    padding: 40px 40px;
}

ks-page-favourites .empty h1 {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 30px;
}

ks-page-favourites .empty ks-button {
    max-width: 300px;
    width: 100%;
}


@media screen and (max-width: 550px) {
    ks-page-favourites .favourites-header {
        flex-wrap: wrap;
        justify-content: center;
    }

    ks-page-favourites .favourites-header > span {
        font-size: 20px;
        line-height: 20px;
    }

    ks-page-favourites .favourites-header .removeAll {
        justify-content: center;
        width: 100%;
        margin-top: 10px;
    }
    
}